.works {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
    margin-bottom: 40px;
}

.works_title {
    margin-bottom: 10px;
    font-family: Montserrat-med;
    font-size: 29px;
    font-weight: normal;
}

.works_link {
    margin-bottom: 10px;
    padding: 4px 0;
    text-decoration: none;
    color: #8fc6ff;
    font-weight: bold;
    position: relative;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}

.works_link:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 1px;
    background: #007cff;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
}

.works_link:after{
    content: '';
    position: absolute;
    bottom: 0;
    right: 50%;
    width: 0;
    height: 1px;
    background: #007cff;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
}

.works_link:hover {
    color: #D2CEFF;
}

.works_link:hover:before {
    width: 50%;
    background: #B88FFF;
}

.works_link:hover:after {
    width: 50%;
    background: #B88FFF;
}
