}

.Contacts_link {
    position: relative;
    color: #8fc6ff;
}

.Contacts_item a:before {
    display: inline-block;
    content: '';
    width: 32px;
    height: 32px;
    position: absolute;
    top: 50%;
    left: -40px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

@media (min-width: 768px) {
    .Contacts_item a:before {
        width: 40px;
        height: 40px;
        left: -55px;
    }
}

@media (min-width: 1000px) {
    .Contacts_item a:before {
        width: 36px;
        height: 36px;
        left: -40px;
    }
}

.Contacts_item:first-child a:before {
