.Contacts {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 20px;
}

.Contacts_title {
    margin-bottom: 20px;
}

@media (min-width: 768px) {
    .Contacts_title {
        font-size: 23px;
    }
}

.Contacts_list {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media (min-width: 768px) {
    .Contacts_list {
        justify-content: center;
    }
}

@media (min-width: 1000px) {
    .Contacts_list {
        flex-direction: row;
    }
}

.Contacts_item {
    list-style: none;
    margin-bottom: 20px;
}

@media (min-width: 768px) {
    .Contacts_item {
        font-size: 23px;
        font-weight: bold;
    }
}

@media (min-width: 1000px) {
    .Contacts_item {
        font-size: 18px;
        font-weight: bold;
        margin: 0 30px;
    }
}

.Contacts_link {
    position: relative;
    color: #8fc6ff;
}

.Contacts_item a:before {
    display: inline-block;
    content: '';
    width: 32px;
    height: 32px;
    position: absolute;
    top: 50%;
    left: -40px;
    transform: translateY(-50%);
}

@media (min-width: 768px) {
    .Contacts_item a:before {
        width: 40px;
        height: 40px;
        left: -55px;
    }
}

@media (min-width: 1000px) {
    .Contacts_item a:before {
        width: 36px;
        height: 36px;
        left: -40px;
    }
}

.Contacts_item:first-child a:before {
    background: url("./assets/telega.svg") no-repeat center;
    background-size: contain;
}

.Contacts_item:nth-child(2) a:before {
    background: url("./assets/whatsup.png") no-repeat center;
    background-size: contain;
}

.Contacts_item:nth-child(3) a:before {
    background: url("./assets/phone.svg") no-repeat center;
    background-size: contain;
}

.Contacts_item:nth-child(4) a:before {
    background: url("./assets/yandex.svg") no-repeat center;
    background-size: contain;
}